import React, { useState } from "react";
import imgRegister from "../../Images/Register.png";
import { FcGoogle } from "react-icons/fc";
import { useLogin } from "../../Hooks/useLogin";
import { StyleLogin } from "../../Styles/StyleLogin";
import { useAuth } from "../../Context/authContext";
import { useNavigate } from "react-router-dom";

const {
  Main,
  Form,
  Section,
  Input,
  Label,
  Buttom,
  Img,
  Paragraph,
  Errors,
  NoAccounts,
  StyleLink,
} = StyleLogin();

const Register = () => {

  const {
    invalidEmail,
    errorPass,
    notUser,
    handleSubmit,
    handleChnade,
  } = useLogin();

  const {logInWithGoogle} = useAuth();

  const navigate = useNavigate();

  const handleGoogleSign = async () => {
    await logInWithGoogle();
    // navigate("/Home");
  };

  return (
    <>
      <Main>
        <Section>
          <Img alt="Tecnologa" src={imgRegister} />

          <Form onSubmit={handleSubmit}>
            <Paragraph>Bienvenidos a Technology</Paragraph>
            {notUser && <Errors>{notUser}</Errors>}

            <Label htmlFor="email">email</Label>
            <Input
              type={"email"}
              name="email"
              placeholder="Ingresa tu Email"
              onChange={handleChnade}
            />
            {invalidEmail && <Errors>{invalidEmail}</Errors>}

            <Label htmlFor="password">Contrasea</Label>
            <Input
              type={"password"}
              name="password"
              placeholder="Ingresa una contrasea"
              onChange={handleChnade}
            />
            {errorPass && <Errors>{errorPass}</Errors>}

            <Buttom>Ingresar</Buttom>

            <NoAccounts>
              No tienes cuenta?{" "}
              <StyleLink to={"/Register"}>Registrarse</StyleLink>
            </NoAccounts>
          </Form>

            <Buttom
              border="1px solid rgba(209, 209, 209, 0.567)"
              bg="white"
              color="black"
              onClick={handleGoogleSign}
            >
              Ingresar Con Google. <FcGoogle />{" "}
            </Buttom>
        </Section>
      </Main>
    </>
  );
};

export default Register;
